/* index */
[data-tooltip] {
	@include hui_tooltip(attr(data-tooltip), 'top', grey);
}
/* .index */

// font-family: 'Roboto', sans-serif;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body, p, span, a, input, div, button {
	font-family: $inter;
}

p, span, a, div, button, li, svg {
	transition: all .3s linear;
}

div, section {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

h1:focus, h2:focus, h3:focus, h4:focus, h5:focus, h6:focus, p:focus, 
span:focus, a:focus, input:focus, button:focus, div:focus {
	outline: none;
}

div, button, input, a, p, span, h1, h2,
h3, h4, h5, h6 {
	margin: 0;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
	text-decoration: none;
}

li {
	list-style-type: none;
}

.wrap-site {
	margin: 0 auto;
	max-width: 1440px;
	min-width: 22.22vw;
	overflow: hidden;
}

.menu-fixed {
	position: fixed !important;
}

.visibility-hidden {
	margin: 0;
	height: 0;
	visibility: hidden;
}

/*.other-page {
	& .header__logolink--word svg {
		fill: $hoverColor;
	}
	& .header__language_menu.active {
		color: $color_fattyText;
	}
}*/

.margin-top-none {
	margin-top: 0 !important;
}

.main {
	width: 100%;
	border-radius: 0 0 6.94vw 6.94vw;
	background-color: #0D4290;
	position: relative;
	&__background {
		width: 35vw;
		height: 35vw;
		border-radius: 50%;
		background: rgba(255, 255, 255, 0.25);
		filter: blur(13.89vw);
		left: -13.19vw;
		top: -10.83vw;
		position: absolute;
		pointer-events: none;
	}
	&__content {
		margin: 0 auto;
		padding: 5.56vw 0 0;
		max-width: 1160px;
		width: 100%;
	}
	&__blockLogotypes,
	&__head {
		padding: 0 2.08vw;
	}
	&__blockLogotypes {
		display: flex;
		justify-content: space-between;
	}
	&__logotype-url {
		width: 19.54545454545455%;
		max-height: 3.47vw;
		display: flex;
	}
	&__logotype {
		width: 100%;
		max-height: 3.47vw;
		&.language {
			width: 19.36363636363636%;
		}
	}
	&__head {
		margin-top: 6.60vw;
		width: 84%;
		font-size: $fs_48px;
		font-weight: $fw_vbig;
		line-height: $lh_58;
		color: white;
	}
	&__program {
		margin-top: 1.39vw;
		padding: 0 1.39vw 2.78vw;
		display: flex;
		flex-wrap: wrap;
	}
	&__iconsblock,
	&__videoblock {
		padding: 0 0.69vw;
		width: 50%;
	}
	&__item {
		margin-top: 1.39vw;
		display: flex;
		align-items: center;
	}
	&__itemicon {
		margin-right: 0.69vw;
		width: 3.33vw;
		height: 3.33vw;
	}
	&__itemtext {
		font-size: $fs_18px;
		line-height: $lh_22;
		color: white;
	}
	&__button {
		margin-top: 2.78vw;
		padding: 1.39vw;
		background-color: #FFE352;
		cursor: pointer;
		font-size: 1.11vw;
		font-weight: $fw_big;
		line-height: $lh_19;
		border-radius: 0.69vw;
		border: none;
	}
	&__videoblock {
		display: flex;
		justify-content: flex-end;
	}
	&__image {
		width: 96.36363636363636%;
	}
	&__video {
		margin-top: 3.82vw;
		width: 100%;
	}
}

#program {
	position: relative;
}

.programtop {
	margin: 8.68vw auto 0;
	padding: 4.17vw 6.25vw 3.82vw;
	max-width: 1100px;
	min-height: 33.33vw;
	border-radius: 1.39vw 1.39vw 0 0;
	background-color: #112133;
	position: relative;
	&__background {
		width: 26.74vw;
		height: 26.74vw;
		top: -3.47vw;
		right: -3.61vw;
		position: absolute;
		pointer-events: none;
	}
	&__head,
	&__text {
		width: 65.21739130434783%;
		color: white;
	}
	&__head {
		font-size: $fs_24px;
		font-weight: $fw_med;
		line-height: $lh_34;
	}
	&__text {
		margin-top: 2.08vw;
		font-size: $fs_18px;
		line-height: $lh_26;
		&:nth-child(2) {
			margin-top: 40px;
		}
	}
}

.programbottom {
	margin: 0 auto;
	padding: 0 6.25vw 4.86vw;
	max-width: 1100px;
	display: flex;
	border-radius: 0px 0px 20px 20px;
	background-color: #F3F6F9;
	&__head {
		margin-top: 4.86vw;
		font-size: $fs_24px;
		line-height: $lh_34;
		color: $accordoin_color;
	}
	&__accordionblock {
		width: 65.21739130434783%;
	}
	&__accordion {
		margin-top: 1.39vw;
	}
	&__paragraph {
		margin-top: 0.69vw;
		padding: 0 0.69vw;
		height: 3.47vw;
		display: flex;
		align-items: center;
		border: 0.07vw solid rgba(118, 148, 174, 0.3);
		border-radius: 0.28vw;
		color: $accordoin_color;
		cursor: pointer;
		&:hover {
			background-color: white;
		}
		&.active {
			background-color: white;
			& .programbottom__arrow {
				transform: scale(1);
			}
		}
	}
	&__arrow {
		margin-right: 0.69vw;
		width: 0.90vw;
		height: 0.56vw;
		transform: scale(-1);
	}
	&__accordionhead {
		font-size: $fs_16px;
		font-weight: $fw_big;
		line-height: $lh_24;
	}
	&__accordoinlist {
		height: 0;
		overflow: hidden;
	}
	&__list {
		padding: 0.69vw 0 1.39vw 1.74vw;
	}
	&__item {
		font-size: $fs_16px;
		line-height: $lh_24;
		list-style-type: disc;
		color: $accordoin_color;
	}
	&__tablet {
		padding-left: 4.17vw;
		width: 34.78260869565217%;
		display: flex;
		justify-content: flex-end;
	}
	&__image {
		margin-top: 3.47vw;
		width: 80.91603053435115%;
		max-height: 20.83vw;
	}
}

.background-block {
	width: 100%;
	height: 46.6887417218543%;
	bottom: 0;
	position: absolute;
	background-color: #E3EAF0;
	z-index: -1;
}

.competition {
	margin-top: 6.60vw;
	min-height: 8.33vw;
	padding: 2.08vw 0;
	display: flex;
	position: relative;
	background: linear-gradient(0deg, #FFE352, #FFE352);
	&__blockimage {
		width: 25.34722222222222%;
		min-height: 100%;
	}
	&__backgroundimage {
		width: 22.5%;
		height: 100%;
		top: 0;
		left: 0;
		bottom: 0;
		position: absolute;
	}
	&__block {
		margin: 0 auto;
		//margin: 0 85px 0 70px;
		padding: 0 1.04vw;
		width: 49.30555555555556%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		z-index: 2;
	}
	/*&__head {
		padding: 0 30px;
		width: 18.18181818181818%;
		font-weight: $fw_med;
		font-size: $fs_30px;
		line-height: $lh_34;
		text-transform: uppercase;
		color: #FE5955;
	}*/
	&__text {
		//padding-right: 15px;
		//width: 70.19730010384216%;
		font-size: $fs_22px;
		font-weight: $fw_min;
		line-height: $lh_28;
		text-align: center;
		color: rgba(0, 0, 0, 0.72);
	}
	&__bold {
		font-weight: $fw_med;
	}
	&__blockbutton {
		padding-left: 2.08vw;
		width: 25.34722222222222%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	&__button {
		padding: 0.69vw 2.08vw;
		//min-width: 241px;
		background-color: #FE5955;
		box-shadow: 0px 4px 24px rgba(162, 54, 51, 0.25);
		border-radius: 1.04vw;
		border: none;
		font-weight: $fw_big;
		font-size: $fs_20px;
		line-height: $lh_28;
		color: white;
		cursor: pointer;
	}
}

@media all and (min-width: 1440px) {
	.main {
		border-radius: 0 0 100px 100px;
		&__background {
			width: 504px;
			height: 504px;
			filter: blur(200px);
			left: -190px;
			top: -156px;
		}
		&__content {
			padding: 80px 0 0;
		}
		&__logotype,
		&__logotype-url {
			max-height: 50px;
		}
		&__blockLogotypes,
		&__head {
			padding: 0 30px;
		}
		&__head {
			margin-top: 95px;
			font-size: 48px;
			line-height: 58px;
		}
		&__program {
			margin-top: 20px;
			padding: 0 20px 40px;
		}
		&__iconsblock,
		&__videoblock {
			padding: 0 10px;
		}
		&__item {
			margin-top: 20px;
		}
		&__itemicon {
			margin-right: 10px;
			width: 48px;
			height: 48px;
		}
		&__itemtext {
			font-size: 18px;
			line-height: 22px;
		}
		&__button {
			margin-top: 40px;
			padding: 20px;
			border-radius: 10px;
			font-size: 16px;
			line-height: 21px;
		}
		&__video {
			margin-top: 55px;
		}
	}
	.programtop {
		margin: 125px auto 0;
		padding: 60px 90px 55px;
		min-height: 480px;
		border-radius: 20px 20px 0 0;
		&__background {
			width: 385px;
			height: 385px;
			top: -50px;
			right: -52px;
		}
		&__head {
			font-size: 24px;
			line-height: 34px;
		}
		&__text {
			margin-top: 30px;
			font-size: 18px;
			line-height: 26px;
		}
	}
	.programbottom {
		padding: 0 90px 70px;
		&__head {
			margin-top: 70px;
			font-size: 24px;
			line-height: 34px;
		}
		&__accordion {
			margin-top: 20px;
		}
		&__paragraph {
			margin-top: 10px;
			padding: 0 10px;
			height: 50px;
			border-width: 1px;
			border-radius: 4px;
		}
		&__arrow {
			margin-right: 10px;
			 width: 13px;
			 height: 8px;
		}
		&__list {
			padding: 10px 0 20px 25px;
		}
		&__item {
			font-size: 16px;
			line-height: 24px;
		}
		&__tablet {
			padding-left: 60px;
		}
		&__accordionhead {
			font-size: 16px;
			line-height: 24px;
		}
		&__accordionhead {
			font-size: 16px;
			line-height: 24px;
		}
		&__image {
			margin-top: 50px;
			max-height: 300px;
		}
	}
	.competition {
		margin-top: 95px;
		min-height: 120px;
		padding: 30px 0;
		&__text {
			font-size: 22px;
			line-height: 28px;
		}
		&__blockbutton {
			padding-left: 30px;
		}
		&__button {
			padding: 10px 30px;
			border-radius: 15px;
			font-size: 20px;
			line-height: 28px;
	 	}
		&__block {
			padding: 0 15px;
		}
	}
}